import Image from "next/image";
import React from "react";
import Logo from "@/assets/images/circle-logo.svg";
import clsx from "clsx";
import styles from "@/styles/header.module.css";

const Header = () => {
  const links = ["Projects", "Services", "Contacts"];
  return (
    <header className="flex items-center pt-[30px] px-[125px] text-xl">
      <Image
        src={Logo}
        alt={""}
        width={40}
        height={40}
        className="mr-[180px]"
      />
      <nav className="flex items-center ">
        {links.map((e) => (
          <a
            key={e}
            href="#"
            className={clsx(styles.nav, "font-medium px-[35px] relative")}
          >
            {e}
          </a>
        ))}
      </nav>
      <a
        href="#"
        className={clsx(
          styles["project-btn"],
          "ml-auto px-6 relative flex items-center justify-center"
        )}
      >
        <div className={styles.circle}></div>
        Start a project
      </a>
    </header>
  );
};

export default Header;
